let self;
import { register, inquire } from "@/api/mine.js"
export default {
	data() {
		return {
			info: {
				nickName: "",
				islogin: 0,
				isgetuserinfo: !1,
				loginnote: 0,
				getcodetimes: 0,
				gettokentimes: 0,

			},
			openId: ""
		};
	},
	methods: {

		async GetUserInfo({ detail }) {
			console.log(detail)
			this.info = { ...JSON.parse(detail.rawData) }
			const res = await register({ ...this.info, openId: this.openId });
			console.log({ ...this.info, openId: this.openId })
			if (res.status === 0) {
				uni.showToast({
					title: "注册成功",
					icon: "none"
				})
			}
			this.$store.commit("user/SET_USER_INFO", this.info);
		},
		/* 运动步数统计 */
		toSportGoal() {
			// uni.showToast({
			// 	icon: "none",
			// 	title: "该功能暂未开放"
			// })
			uni.navigateTo({
				url: "./step/index"
			})
		},
		/* 密码设置 */
		toChangePwd() {
			uni.showToast({
				icon: "none",
				title: "该功能暂未开放"
			})
		},
		/* 常见问题 */
		Tousetips() {
			uni.navigateTo({
				url: './useTip/index'
			});

		},
		/* 权限设置 */
		openset() {
			uni.openSetting({
				success(res) {
					console.log(res.authSetting)
				}
			});
		},
		/* 需要智能设备 */
		apploadnote() {
			uni.showToast({
				icon: "none",
				title: "该功能暂未开放"
			})
		},




	},
	onShow() {
		this.openId = this.$store.getters.openId;
		this.info = this.$store.getters.userInfo || {
			nickName: "",
			islogin: 0,
			isgetuserinfo: !1,
			loginnote: 0,
			getcodetimes: 0,
			gettokentimes: 0,
		};
		console.log(this.openId)
		// console.log("你好")
		self = this;

	}
}
